import Vue from 'vue';
import Router from 'vue-router';
import Ping from '../components/Ping.vue';
import Orders from '../components/Orders.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/orders',
      name: 'Orders',
      component: Orders,
    },
    {
      path: '/ping',
      name: 'Ping',
      component: Ping,
    },
  ],
});
